import React, { useState, useEffect, useContext } from 'react'
import { useLocation, Link } from 'react-router-dom'

import DarkModeContext from '../../../Context/DarkModeContext'
import HeaderTitleContext from '../../../Context/HeaderTitleContext'

import Logo from '../../../g-git.svg'

import '../Header/Header.style.scss'

const Header = () => {

    const location = useLocation()
    const { pathname } = location
    //console.log(pathname)

    const { darkMode, handleDarkMode } = useContext(DarkModeContext)
    const { infoHeader, handleInfoHeader } = useContext(HeaderTitleContext)
    //console.log(data)
    //console.log(infoHeader)

    return (
        <div className={`head  w-full inline-flex justify-between items-center ${darkMode? 'bg-Oxford_Blue_3' : 'bg-Dark_Cornflower_Blue' }`}>
            <h1>
                {
                pathname === '/G-git' ? 'G-git' :
                pathname === `/user/${infoHeader}/repos` ? `${infoHeader}'s Repos` :
                pathname === `/user/${infoHeader}/following` ? `${infoHeader} follows` :
                pathname === `/user/${infoHeader}/followers` ? `${infoHeader}'s followers` :
                'G-git'
                }
            </h1>
            {/*<button onClick={handleDarkMode}>
                DarkMode: {darkMode? 'on' : 'off'}
            </button>*/}
            <Link to='/G-git'><img src={Logo} alt="Logo" /></Link>
        </div>
    )
}

export default Header
